import React from 'react';
import request from "../api/api.js";
import { Link } from "react-router-dom";
import { addFavorite, deleFavorite } from "../api/favorite";


class Home extends React.Component {
  constructor(props){
      super(props)
      this.state = {
         data : {
            articles : [],
            
         },
         rows:10, //每页条数
         totalPage:[],//总页数
         currentPage:1, //当前页

         //数据在加载过程中点赞禁止
        favoritedisabled : false
      }
      this.fnFavorite = this.fnFavorite.bind(this)
      
  }
  componentDidMount(){
     request({
        methos : "GET",
        url : "/api/articles",
        params : {
          limit:this.state.rows
        }
     }).then((res) =>{
       console.log(res,"res22222222222")
       this.setState({
           data : res.data
       })
       this.setState({
        totalPage : res.data.articlesCount % this.state.rows === 0 ? res.data.articlesCount / this.state.rows : res.data.articlesCount % this.state.rows+1
       })
     })

  }
  async fnFavorite(data){
    await this.setState({
      //数据在加载过程中点赞禁止
      favoritedisabled : true
    })
    console.log(data,'---------')
    if(data.favorited){
       //取消点赞
       //数据在加载过程中点赞禁止
      
       await deleFavorite(data).then((res)=>{
          console.log(res,"删除")
       })
       data.favorited = false
       data.favoritesCount += -1

       let key = this.state.data.articles.indexOf(data)
       this.state.data.articles[key] = data
       await this.setState({
          articles : this.state.data.articles
       })
    }
    else{
      //取消点赞
        await addFavorite(data).then((res)=>{
          console.log(res,"添加")
      })
      data.favorited = true
      data.favoritesCount += 1

      let key = this.state.data.articles.indexOf(data)
       this.state.data.articles[key] = data
       await this.setState({
          articles : this.state.data.articles
       })
    }
    await this.setState({
      //数据在加载过程中点赞禁止
      favoritedisabled : false
    })
    
  }
  click= (index)=>{
    console.log(index)
    this.setState({
      currentPage:index+1
    },function(){
      request({
        methos : "get",
        url : "/api/articles" ,
        params : {
          limit:this.state.rows,
          offset:index * this.state.rows
        }
     }).then((res) =>{
       console.log(res,"res22222222222")
       this.setState({
           data : res.data
       })
       this.setState({
        totalPage : res.data.articlesCount % this.state.rows === 0 ? res.data.articlesCount / this.state.rows : res.data.articlesCount % this.state.rows+1
       })
     })
    })
  }
 render(){
   return (
    <div className="home-page">

    <div className="banner">
      <div className="container">
        <h1 className="logo-font">conduit</h1>
        <p>A place to share your knowledge.</p>
      </div>
    </div>
  
    <div className="container page">
      <div className="row">
  
        <div className="col-md-9">
          <div className="feed-toggle">
            <ul className="nav nav-pills outline-active">
              <li className="nav-item">
                <a className="nav-link disabled" href="">Your Feed</a>
              </li>
              <li className="nav-item">
                <a className="nav-link active" href="">Global Feed</a>
              </li>
            </ul>
          </div>
  
          {
            this.state.data.articles.map((item,index) =>{
               return(
                <div className="article-preview" key={index}>
                <div className="article-meta">
                  <a href="profile.html"><img src={item.author.image} /></a>
                  <div className="info">
                    <a href="" className="author">{item.author.username}</a>
                    <span className="date">{item.createdAt}</span>
                  </div>
                  <button className="btn btn-outline-primary btn-sm pull-xs-right" onClick={()=>{
                     this.fnFavorite(item)
                  }}>
                    <i className="ion-heart"></i> {item.favoritesCount}
                  </button>
                </div>
                <a href="" className="preview-link">
                  <h1>{item.title}</h1>
                  <p>{item.description}</p>
                  <span>Read more...</span>
                </a>
              </div>
               )
            })
          }
          {
            Array.from({length:this.state.totalPage}).map((item,index)=>{
              return(
                <li key={index} className={this.state.currentPage === index+1 ? "page-item active":"page-item"} >
                 <Link className="page-link " to="/" onClick={this.click.bind(null,index)}>{index+1}</Link>
              </li>
              )
            })
          }
        </div>
  
        <div className="col-md-3">
          <div className="sidebar">
            <p>Popular Tags</p>
  
            <div className="tag-list">
              <a href="" className="tag-pill tag-default">programming</a>
              <a href="" className="tag-pill tag-default">javascript</a>
              <a href="" className="tag-pill tag-default">emberjs</a>
              <a href="" className="tag-pill tag-default">angularjs</a>
              <a href="" className="tag-pill tag-default">react</a>
              <a href="" className="tag-pill tag-default">mean</a>
              <a href="" className="tag-pill tag-default">node</a>
              <a href="" className="tag-pill tag-default">rails</a>
            </div>
            
          </div>
        </div>
      </div>
    </div>
  </div>
   )
 }
}

export default Home